import React, { Fragment } from 'react';

async function getWasm() {
  return new Promise((resolve, reject) => {
    var db = window.indexedDB.open('wasm_app_cache');
    db.onerror = (error) => error && reject(error);
    db.onsuccess = () => {
      var cache = db.result.transaction('cache', 'readonly').objectStore('cache');
      var js = cache.get('js');
      var wasm = cache.get('wasm');
      js.onerror = (error) => error && reject(error);
      wasm.onerror = (error) => error && reject(error);
      js.onsuccess = () => wasm.onsuccess = () => resolve([js.result.value, wasm.result.value]);
    };
  });
}

async function saveWasm(js, wasm) {
  var db = window.indexedDB.open('wasm_app_cache');
  db.onupgradeneeded = () => db.result.createObjectStore('cache', {keyPath: 'key'});
  db.onsuccess = () => {
    var cache = db.result.transaction('cache','readwrite').objectStore('cache');
    cache.put({key: 'js', value: js});
    cache.put({key: 'wasm', value: wasm});
  };
}

function initWasm(js, wasm, Module) {
  Module.wasmBinary = wasm;
  new (Function.prototype.bind.apply(Function, [null, 'Module', js]))()(Module);
}

async function loadWasm(js_response, wasm_response, mount_component, server_url, session, modules) {
  let js, wasm;
  try {
    [js, wasm] = await Promise.all([(await js_response).text(), (await wasm_response).arrayBuffer()]);
    saveWasm(js, wasm);
  }
  catch (error) {
    try {
      [js, wasm] = await getWasm();
      console.error(error);
    }
    catch (cache_error) {
      window.alert("There's an error in loading the app.");
      console.error(cache_error);
      throw error;
    }
  }
  const client_modules = await modules;
  const Module = {mount_component, ...client_modules};
  initWasm(js + session.custom_js, wasm, Module);
  await Module.orm_loaded;
  Module.orm.set_server_url(new URL(server_url).href.slice(0, -1));
  Module.orm.set_user(session.result.id, session.login, session.password);
  window.Module = Module;
}

export default function (props) {
  //const [loaded, setLoaded] = React.useState(false);
  const [component, setComponent] = React.useState(false);
  React.useEffect(() => loadWasm(props.js, props.wasm, setComponent, props.server_url, props.session, props.client_modules) && (() => {}), []);
  if (component) return component;
  return <Fragment/>;
}
