import React, { Fragment } from 'react';
import { Admin, Resource } from 'react-admin';
import { sha256 } from 'js-sha256';
import { createMuiTheme, makeStyles } from '@material-ui/core/styles';
import { ThemeProvider, Backdrop, CircularProgress, Button, TextField, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle } from '@material-ui/core';
import PouchDB from 'pouchdb-browser';
import Loaders from './Loaders';

//document.head.insertAdjacentHTML('beforeend', `<style>[class^='RaLogin-avatar-'] {background-image: url('./logo192.png')}</style>`);

window.React = React;
window.PouchDB = PouchDB;

async function loadModules() {
  const [Core, Icons, Styles, Admin, Router] = await Promise.all([import('@material-ui/core'), import('@material-ui/icons'), import('@material-ui/core/styles'), import('react-admin'), import('react-router-dom')])
  return {Core, Icons, Styles, Admin, React, Router};
}

const clientModules = loadModules();

async function checkSetup(mount_component) {
  const setup = await fetch('./setup.js');
  if (!setup.ok || setup.headers.get('Content-Type').indexOf('html') !== -1) return;
  const code = await setup.text();
  const modules = await clientModules;
  modules.mount_component = mount_component;
  const module_names = Object.keys(modules);
  new (Function.prototype.bind.apply(Function, [null, ...module_names, code]))()(...module_names.map(name => modules[name]));
}

async function check(url) {
  try {
    const response = await fetch(url + 'api/check');
    const json = await response.json();
    if (json.status === 'success' && json.orm_compiler_python_version) return true;
  }
  catch (error) {
    return false;
  }
  return false;
}

async function save_session(new_session) {
  const db = new PouchDB('session');
  new_session._id = '_local/session';
  try {
    const session = await db.get('_local/session');
    new_session._rev = session._rev;
  }
  catch (error) {}
  return db.put(new_session);
}

async function login_orm(login, password, url, setComponent, session) {
  url = new URL(url).href;
  //let session;
  try {
    const response = await fetch(url + 'api/login', {headers: {'Content-Type': 'application/json'}, method: 'POST', body: JSON.stringify({login, password})});
    const result = await response.json();
    if (result.status !== 'success') return Promise.reject(result);
    localStorage.orm_server_url = url;
    session = result;
    session.login = login;
    session.password = password;
    await save_session(result);
  }
  catch (error) {
    console.error(error);
    //session = await new PouchDB('session').get('_local/session');
  }
  setComponent(<Loaders js={fetch(url + 'api/orm/js', {headers: {'Authorization': 'Basic ' + btoa(login + '|:|' + password)}})} wasm={fetch(url + 'api/orm/wasm', {headers: {'Authorization': 'Basic ' + btoa(login + '|:|' + password)}})} client_modules={clientModules} server_url={url} session={session}/>);
}

async function logout() {
  return PouchDB('session').destroy();
}

const resolve = () => Promise.resolve();
const reject = () => Promise.reject();

const authProvider = {logout: resolve, checkError: reject, checkAuth: reject, getPermissions: reject};

const theme = createMuiTheme({palette: {primary: {main: /*'#009688'*/ '#00A09D'}, secondary: {main: '#00A09D' /*'#875A7B'*/}}});

const useStyles = makeStyles();

/*const useStyles = makeStyles((theme) => ({
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    color: '#fff',
  },
}));*/

const createPromise = () => {
  const promise = [];
  promise[0] = new Promise((resolve) => promise[1] = resolve);
  return promise
}

const input = {};

function App() {
  const [component, setComponent] = React.useState(null);
  //const [backdrop, setBackdrop] = React.useState(false);
  const [state, setState] = React.useState({open: false});
  const [loaded, setLoaded] = React.useState(false);
  React.useEffect(() => (new PouchDB('session').get('_local/session').then((session) => login_orm(session.login, session.password, localStorage.orm_server_url, setComponent, session)).catch(() => checkSetup(setComponent).catch(console.warn) && setLoaded(true))) && (() => {}), []);
  if (component) return component;
  if (!loaded) return <Fragment/>;
  //const classes = useStyles();
  else if (!window.location.hash || (window.location.hash === '#/')) window.location.hash = '#/login';
  const set_server_url = () => {
    const [url_promise, url_resolve] = createPromise();
    setState({open: true, url_resolve});
    return url_promise;
  }
  const login = async ({username, password}) => {
    username = await sha256(username);
    password = await sha256(username + password);
    if (window.custom_configured_orm_server_url) return login_orm(username, password, window.custom_configured_orm_server_url, setComponent);
    if (localStorage.orm_server_url) return login_orm(username, password, localStorage.orm_server_url, setComponent);
    const current_url = ((href) => href.substring(0, href.lastIndexOf('/')) + '/')(window.location.origin + window.location.pathname);
    const current_origin = window.location.origin + '/'
    const same = current_url === current_origin;
    const checks = await Promise.all(same ? [check(current_url)] : [check(current_url), check(current_origin)]);
    let url;
    if (same) {
      const [subpath, origin] = checks;
      if (subpath) url = current_url;
      else if (origin) url = current_origin;
    }
    else if (checks[0]) url = current_url;
    if (!url) url = await set_server_url();
    console.log(url);
    return login_orm(username, password, url, setComponent);
  };
  authProvider.login = login;
  return (
    <Fragment>
      <Admin theme={theme} authProvider={authProvider} dataProvider={{}}>
        <Resource name="login"/>
      </Admin>
      {/*<Backdrop className={classes.backdrop} open={backdrop} onClick={() => setBackdrop(false)}>
        <CircularProgress color="inherit" />
      </Backdrop>*/}
      <ThemeProvider theme={theme}>
      <Dialog open={state.open} aria-labelledby="form-dialog-title">
        <DialogTitle id="form-dialog-title">Setup</DialogTitle>
        <DialogContent>
          <DialogContentText>
            It seems the Server URL is not configured for you yet. Ask your Administrator for the URL
          </DialogContentText>
          <TextField color={'secondary'} autoFocus margin="dense" id="server_url" label="Server URL" type="url" fullWidth onKeyPress={(event) => event.key === 'Enter' ? [event.preventDefault(), setState({open: false}), state.url_resolve(input.url)] : undefined} onChange={(event) => input.url = event.target.value}/>
        </DialogContent>
        <DialogActions>
          <Button color={'secondary'} onClick={() => [setState({open: false}), state.url_resolve(input.url)]}>Set</Button>
        </DialogActions>
      </Dialog>
      </ThemeProvider>
    </Fragment>
  );
}

export default App;
